



































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../../heating-config";
import axios from 'axios';
import moment from 'moment';

export default Vue.extend({
  data() {
    return {
      constData:[],
      pagination: {
        sortBy: "group", // String, column "name" property value
        descending: false,
        page: 1,
        rowsPerPage: 50 // current rows per page being displayed
      },
      columns: [
        {
          name: 'group',
          label: 'Skupina',
          align: 'left',
          field: 'group',
          sortable: true,
        },
        {
          name: 'refCd',
          label: 'Kľúč',
          align: 'left',
          field: 'refCd',
          sortable: true,
        },{
          name: 'description',
          label: 'Popis',
          align: 'left',
          field: 'description',
          sortable: true,
        }, {
          name: 'value',
          label: 'Hodnota',
          align: 'left',
          field: 'value',
          sortable: true,
        }
      ],
      refreshIntervalId: null,
    }
  },
  methods: {
    loadCurrentState() {
      axios.get(cfg.BASE_URL + "const")
          .then(response => {
            this.constData= response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    modify(row:any){
      axios.put(cfg.BASE_URL + "const?type=" + row['constantType'], row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            alert(error)
          });
    }
  },
  mounted(): void {
    this.loadCurrentState();
  }
});
