












































































































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../../heating-config";
import axios from 'axios';
import moment from 'moment';

require('url-search-params-polyfill');

export default Vue.extend({
  data() {
    return {
      addHeating:false,
      fromTime:null,
      toTime:null,
      diffStart:10,
      diffStop:0,
      boilerBlocked:false,
      modifyFor: moment().isoWeekday().toString(),
      serverPagination: {
        page: 1,
        rowsNumber: 50 // specifying this determines pagination is server-side
      },
      selectOptions: [
        {
          label: 'Pondelok',
          value: '1'
        },
        {
          label: 'Utorok',
          value: '2'
        },
        {
          label: 'Streda',
          value: '3'
        },
        {
          label: 'Štvrtok',
          value: '4'
        },
        {
          label: 'Piatok',
          value: '5'
        },
        {
          label: 'Sobota',
          value: '6'
        },
        {
          label: 'Nedeľa',
          value: '7'
        }
      ],
      columns: [{
        name: 'fromTime',
        required: true,
        label: 'Od (čas)',
        align: 'left',
        field: 'fromTime',
        sortable: true,
      }, {
        name: 'toTime',
        required: true,
        label: 'Do (čas)',
        align: 'left',
        field: 'toTime',
        sortable: true,
      }, {
        name: 'threeWayValveStartDiff',
        required: true,
        label: 'Otvoriť 3-cestný ventil pri differencii',
        align: 'threeWayValveStartDiff',
        field: 'hor',
        sortable: true,
      }, {
        name: 'threeWayValveStopDiff',
        required: true,
        label: 'Zatvoriť 3-cestný ventil pri differencii',
        align: 'left',
        field: 'threeWayValveStopDiff',
        sortable: true,
      }, {
        name: 'boilerBlock',
        required: true,
        label: 'Ohrev teplej vody blokovaný',
        align: 'left',
        field: 'boilerBlock',
        sortable: true,
      }],
      rowSelected: [],
      tableData: [],
    }
  },
  methods: {
    formatTime(time:string){
      return moment(time).format('HH:mm:ss');
    },
    loadCurrentState() {
      Loading.show();
      let url = cfg.BASE_URL + "heating/query/forDay/" + this.modifyFor;
      axios.get(url)
          .then(response => {
            this.tableData = response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    editHeating(row: any) {
      axios.put(cfg.BASE_URL + "heating/cmd/update/" + row['id'], row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },
    saveSolarHeating(){
      Loading.show();
      var newItem  ={
        'fromTime':this.fromTime,
        'day':this.modifyFor,
        'toTime':this.toTime,
        'boilerBlock':this.boilerBlocked,
        threeWayValveStartDiff:this.diffStart,
        threeWayValveStopDiff:this.diffStop
      };
      axios.post(cfg.BASE_URL + "heating/cmd/create",newItem, {method: "POST"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    },

    deleteHeating() {
      Loading.show();
      axios.delete(cfg.BASE_URL + "heating/cmd/delete/" + this.rowSelected[0]['id'], {method: "DELETE"})
          .then(response => {
            this.loadCurrentState();
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            alert(error)
          });
    }
  },
  mounted(): void {
    this.loadCurrentState();
  }
});
